.page-error {
    margin-bottom: 60px;
    margin-top: 80px;
    display: flex;
    align-items: center;
    flex-direction: row;

    &__title {
        font-family: $bold-f; }

    &__subtitle {
        font-size: 28px; }

    &-404 {
        position: relative;
        margin-top: 60px;
        padding-top: 0;

        &__text-wrap {
            padding-bottom: 200px; }

        &__title {
            margin-bottom: 20px;
            font-size: 56px; }

        &__subtitle {
            width: 365px; }

        &__btn {
            width: 140px;
            margin-top: 150px;

            &::before {
                display: inline-block;
                margin-right: 20px;
                transform: rotate(90deg);
                vertical-align: -2px; } }


        &__img {
            width: 940px;
            position: absolute;
            top: 50px;
            right: 0; } }

    &-500 {
        margin: 60px 15px;
        align-items: center;
        justify-content: center;

        &__title {
            margin-top: 0;
            margin-bottom: 40px;
            font-size: 48px;
            text-align: center; }

        &__subtitle {
            max-width: 450px;
            text-align: center;
            margin: 0 auto; }

        &__img {
            display: block;
            width: 100%;
            max-width: 800px;
            margin-bottom: 60px; } } }

.maintenance-mode {
    display: flex;
    min-height: 100vh;
    padding: 15px;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    background-image: url(../images/maintenance-mode.jpg);
    background-color: $dark-c;
    background-size: cover;
    background-position: center center;
    color: $light-c;

    &__logo {
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 48px;
        width: 380px; }

    &__title {
        font-family: $bold-f;
        font-size: $huge-fz;
        margin-bottom: 24px; }

    &__text-wrap {
        max-width: 500px;
        margin: 0 auto;
        color: $light-c; } }
